import { ErrorMessage, Field, Form, Formik } from "formik";
import React from "react";
import styles from './MyPost.module.css'
import Post from "./Post/Post";
import * as Yup from 'yup';
import { Component } from "react";


class MyPost extends Component {
  
  render() {
    
    let postsElements = this.props.posts.map(p => <Post message={p.message} id={p.id} likes={p.likes} profile={this.props.profile}/>)
    return (
        
      <div className={styles.postBlock}>
        {this.props.isOwner && 
        <div className={styles.addPost}>
          <AddPostForm addPost={this.props.addPost} />
        </div>}
        
        <div className={styles.posts}>
          {postsElements}

        </div>
      </div>

    )
  }
}
const AddPostForm = (props) => {
  return (
    <Formik
      initialValues={{ postText: '' }}
      onSubmit={(values) => {
        props.addPost(values.postText)
      }}
      validationSchema={Yup.object({
        postText: Yup.string().max(1000, 'Must be 1000 characters or less')
      })}>
      <Form>
        <div className={styles.postTextArea}>
        <Field name='postText' as='textarea' placeholder='Whats up?' />
        <ErrorMessage name="postText" className={styles.loginErrors} />
        </div>
        <div className={styles.postButtonBlock}>
          <button className={styles.postButton} type="submit">Post</button>
        </div>
      </Form>
    </Formik>
  )
}

export default MyPost;