const SEND_MESSAGE = "SEND-MESSAGE"



let initialState = {
    messages: [
        { id: 1, message: "Hi" },
        { id: 2, message: "LMAO" },
        { id: 3, message: "LMAO" },
        { id: 4, message: "LMAO" },
        { id: 5, message: "How are you?" },
        { id: 5, message: "bye" }
    ],
    dialogs: [
        { id: 1, message: "Hi" , name: "", avatar: "https://w-dog.ru/wallpapers/1/33/483353078580185/koshki-krasnyj-vid-glaza-kot.jpg" },
        { id: 2, message: "LMAO", name: "", avatar: "https://proprikol.ru/wp-content/uploads/2020/08/krasivye-kartinki-kotikov-47.jpg" },
        { id: 3, message: "bye", name: "", avatar: "https://aroundpet.ru/wp-content/uploads/u-koshki-rvotnyj-refleks-1170x767.jpg" },
        { id: 4, message: "LMAO",name: "", avatar: "https://w-dog.ru/wallpapers/5/18/428517343694663/kot-kotyara-morda-stul.jpg" },
        { id: 5,  message: "How are you?", name: "", avatar: "https://kot-pes.com/wp-content/uploads/2018/12/post_5c17b46c61f75.jpeg" }
    ]
}


const dialogsReducer = (state = initialState, action) => {


    switch (action.type) {

        case SEND_MESSAGE:
            console.log(action.values)
            
            let mes = {
                id: state.messages.length + 1,
                message: action.values
            };
           
            return {
                ...state,
                messages: [...state.messages, mes]
            }


        default:
            return state;
    }
}

export const sendMessageCreator = (values) => ({
    type: SEND_MESSAGE,
    values: values
})
export default dialogsReducer;