import React from "react";
import { Formik, useField, Form } from 'formik'
import * as Yup from 'yup';
import styles from "./Login.module.css"
import { connect } from "react-redux";
import  {login, logout} from "../../redux/authReducer";
import {Navigate} from "react-router-dom";

const Login = (props) => {
  if (props.isAuth)
  {
  return <Navigate to = {'/profile'}/>}
  return <div className={styles.login}>
    <h1>Login</h1>
    <LoginForm login = {props.login}/>
  </div>
}


const LoginForm = (props) => {
  return (
    <Formik
      initialValues={{ login: '', password: '', acceptedTerms: false, }}
      onSubmit={(values) => {
       props.login(values.login, values.password, values.acceptedTerms)
      //  console.log(values.login, values.password, values.acceptedTerms)
      }}
      validationSchema={Yup.object({
        login: Yup.string().email('Invalid email address').required('Required'),
        password: Yup.string()
          .min(2, 'Must be 2 characters or more')
          .required('Required'),
      })}
    >
      <Form>
        <div className={styles.formLogin}>
        <MyTextInput
          label="Email"
          name="login"
          type="text"
          placeholder="examle@mail.com"
        />
        <MyTextInput
          label="Password"
          name="password"
          type="password"
          placeholder="qwerty123"
        />
        <MyCheckbox
          name="acceptedTerms"
        />remember Me
        <div>
        <button type="submit">Login</button>
        </div>
        </div>
      </Form>

    </Formik>
  )
}

const MyTextInput = ({ label, ...props }) => {

  const [field, meta] = useField(props);
  return (
    <>
      <label htmlFor={props.id || props.name}>{label}</label>
      <div >
      <input {...field} {...props} />
      </div>
      {meta.touched && meta.error ? (
        <div className={styles.loginErrors}>{meta.error}</div>
      ) : null}
    </>
  );
};
const MyCheckbox = ({ children, ...props }) => {

  const [field, meta] = useField({ ...props, type: 'checkbox' });
  return (

      <label>
        <input type="checkbox" {...field} {...props} />
        {children}
      </label>

  );
};

const mapStateToProps = (state) =>({
  isAuth: state.auth.isAuth
})
export default connect(mapStateToProps, {login, logout})(Login)