import * as axios from 'axios'
import { savePhoto } from '../redux/profileReducer';

const instance = axios.create({
    withCredentials: true,
    baseURL: 'https://social-network.samuraijs.com/api/1.0/',
    headers: {
    'API-KEY': '0576f9ca-e369-463f-8566-49647240207d'
    }
})

export const usersAPI = {
    getUsers(currentPage = 1, pageSize = 10) {
        return instance.get(`users?page=${currentPage}&count=${pageSize}`).then(response => {
            return response.data
        });
    },
    follow(id = 1) {
        return instance.post(`https://social-network.samuraijs.com/api/1.0/follow/${id}`, {}, {
            withCredentials: true,
            headers: {
                'API-KEY': '0576f9ca-e369-463f-8566-49647240207d'
            }
        })
    },
    unfollow(id = 1) {
        return instance.delete(`https://social-network.samuraijs.com/api/1.0/follow/${id}`, {
            withCredentials: true,
            headers: {
                'API-KEY': '0576f9ca-e369-463f-8566-49647240207d'
            }
        })
    },
    getProfile(userId) {
        console.log('Old method, pls check api js ')
        return profileAPI.getProfile(userId)
    }
}
export const profileAPI = {
    
    getProfile(userId) {
        console.log('id profile= '+userId)
        return instance.get(`profile/${userId}`)
    },
    getStatus (userId){
        return instance.get(`profile/status/${userId}`)
    },
    updateStatus (status){
        console.log(status)
        return instance.put('profile/status', {status: status})
    },
    savePhoto (photoFile){
        var formData = new FormData();
        formData.append("image", photoFile)
        return instance.put('profile/photo', formData, {headers: {
        'Content-Type': 'multipart/form-data'}
        })
    },
    saveProfile (skills, vk, inst){
        console.log("api ="+skills)
        // return instance.put('profile', {"lookingForAJobDescription": skills} )
       return instance.put('profile', {'aboutMe': 'about me 2', 'lookingForAJobDescription': skills, 'fullName': 'myshanyatko', "contacts":{vk: vk ,instagram: inst}})
        
    }
}


export const authAPI = {
    me() {
        return instance.get(`auth/me`)
    },
    login (email, password, rememberMe = false) {
        return instance.post(`auth/login`, {email, password, rememberMe})
    },
    logout () {
        return instance.delete(`auth/login`)
    }
}
