import React from "react";
import MyPostContainer from "./Posts/MyPostContainer";
import s from "./Profile.module.css"
import ProfileInfo from "./ProfileInfo";


const Profile = (props) => {
  return (
    
    <div>
      <ProfileInfo profile={props.profile} saveProfile={props.saveProfile} savePhoto={props.savePhoto} isOwner={props.isOwner} status={props.status} updateStatus={props.updateStatus}/>
      <MyPostContainer isOwner={props.isOwner}   profile={props.profile} />
    </div>
  )
}

export default Profile;