import { profileAPI, usersAPI } from "../api/api"
const ADD_POST = "ADD-POST"
const UPDATE_NEW_POST_TEXT = "UPDATE-NEW-POST-TEXT"
const SET_USER_PROFILE = "SET-USER-PROFILE"
const SET_STATUS = "SET-STATUS"
const DELETE_POST = "DELETE-POST"
const SAVE_PHOTO_SUCCESS = "SAVE-PHOTO-SUCCESS"

let initialState = {
    posts: [
        { id: 1, message: "meow!", likes: 23 },
        { id: 2, message: "mrrrr", likes: 100 }
    ],
    newPostText: '',
    profile: null,
    status: ''
}

const profileReducer = (state = initialState, action) => {
    switch (action.type) {
        case ADD_POST: {
            let post = {
                id: 3,
                message: action.postText,
                likes: 0
            };
            return {
                ...state,
                posts: [...state.posts, post]
            }
        }
        case SET_USER_PROFILE: {

            return {
                ...state,
                profile: action.profile
            }
        }
        case SET_STATUS: {

            return {
                ...state,
                status: action.status
            }
        }
        case DELETE_POST: {

            return {
                ...state,
                posts: state.posts.filter(p => p.id != action.id)
            }
        }
        case SAVE_PHOTO_SUCCESS: {
            return {...state, profile: {...state.profile, photos: action.photos}}
        }
        default:
            return state;

    }
}
export const addPost = (postText) => ({
    type: ADD_POST,
    postText
})
export const deletePost = (postId) => ({
    type: DELETE_POST,
    id: postId
})
export const savePhotoSuccess = (photos) => ({
    type: SAVE_PHOTO_SUCCESS,
    photos: photos
})
export const setUserProfile = (profile) => ({
    type: SET_USER_PROFILE,
    profile
})
export const setStatus = (status) => ({
    type: SET_STATUS,
    status
})
export const getStatus = (userId) => async (dispatch) => {
    {
        let response = await profileAPI.getStatus(userId)
        dispatch(setStatus(response.data))
    }
}
export const updateStatus = (status) => async (dispatch) => {
    {
        let response = await profileAPI.updateStatus(status)
        if (response.data.resultCode === 0) {
            console.log('    ')
            dispatch(setStatus(status))
        }
        else console.log('     ')
    }
}
export const savePhoto = (file) => async (dispatch) => {
    {
        let response = await profileAPI.savePhoto(file);
        
        if (response.data.resultCode === 0) {
            dispatch(savePhotoSuccess(response.data.data.photos))
        }
    }
}
export const saveProfile = (skills, vk, inst) => async (dispatch, getState) => {
       const userId = getState().auth.id
        const response = await profileAPI.saveProfile(skills, vk, inst);
        console.log('vk= '+vk, 'inst= '+inst)
        if (response.data.resultCode === 0) {
             dispatch(getUserProfile(userId))
        }
    
}
export const getUserProfile = (userId) => async (dispatch) => {
    {
        let response = await usersAPI.getProfile(userId)
        dispatch(setUserProfile(response.data));
    }
}

export default profileReducer;