import React from "react";
import Preloader from "../common/Preloader/Preloader";
import s from "./ProfileInfo.module.css"
import ProfileStatusWithHooks from "./ProfileStatusWithHooks";
import userPhoto from "../../assets/images/avatar.jpg"
import logoVk from "../../assets/images/logoVk.svg"
import logoInst from "../../assets/images/logoInstagram.svg"
import { useState } from "react";
import { Formik, Field, Form } from 'formik'



const ProfileInfo = ({ profile, status, updateStatus, isOwner, savePhoto, saveProfile }) => {

    let [editMode, setEditMode] = useState(false);
    const deactivateEditMode = () => {
        setEditMode(false);
    }
    if (!profile) {
        return <Preloader />

    }
    const onMainPhotoSelected = (e) => {
        if (e.target.files.length) {
            savePhoto(e.target.files[0])
        }
    }
    return (
        <div className={s.profileInfo}>
            <div className={s.photo}>
                <img className={s.avatar} src={profile.photos.large || userPhoto}></img>
                {isOwner && <div className={s.fileInput}><label for="input__file">change photo</label> <input id="input__file" onChange={onMainPhotoSelected} type={'file'}></input> </div>}

            </div>
            <div className={s.profileData}>
                <p className={s.fullName}>{profile.fullName}</p>
                <ProfileStatusWithHooks status={status} updateStatus={updateStatus} />
                {editMode ? <ProfileDataForm profile={profile} deactivateEditMode={() => { deactivateEditMode() }} saveProfile={saveProfile} />
                    : <ProfileData isOwner={isOwner} profile={profile} activateEditMode={() => { setEditMode(true) }} />}

            </div>
        </div>
    )
}
const ProfileData = ({ profile, activateEditMode, isOwner }) => {
    let inst = profile.contacts.instagram
    if(inst) {inst = '@'+inst.replace('https://www.instagram.com/', '')}
    else inst = '-'
    console.log(inst)
    let vk = profile.contacts.vk
    if(vk) {vk = '@'+vk.replace('https://vk.com/', '')}
    else vk = '-'
    return (
        <div className={s.desriptionBlockData}>
            <div className={s.contactsCountry}>
                <div className={s.contacts}>
                    <div className={s.contactsEdit}>
                    <p className={s.contactsName}>contacts</p>
                    {isOwner && <button className={s.editButton} onClick={activateEditMode}>edit</button>}</div>
                    <div className={s.contactsItem}>
                        <img src={logoInst}></img>
                        {profile.contacts.instagram ? <a href={profile.contacts.instagram} className={s.contactsItem}>{inst}</a> : <a  className={s.contactsItem}>---</a>}
                    </div>
                    <div className={s.contactsItem}>
                        <img src={logoVk}></img>
                        {profile.contacts.vk ? <a href={profile.contacts.vk} >{vk}</a> : <a  className={s.contactsItem}>---</a>}
                    </div>

                </div>
                <div className={s.country}>
                    <p className={s.countryName}>skills</p>
                    {profile.lookingForAJobDescription ? <p className={s.countryItem}>{profile.lookingForAJobDescription}</p>: <p className={s.countryItem}>---</p>}
                </div>
            </div>
            
        </div>)
}
const ProfileDataForm = ({ profile, deactivateEditMode, saveProfile }) => {

    return (
        <Formik
            initialValues={{ skills: profile.lookingForAJobDescription, inst: profile.contacts.instagram, vk: profile.contacts.vk }}
            onSubmit={(values) => {
                saveProfile(values.skills, values.vk, values.inst)
                deactivateEditMode()
                console.log('skills in ProfileDataForm= ' + values.skills)
            }}
        >

            <Form>
                <div className={s.desriptionBlock}>
                    <div className={s.contactsCountry}>
                        <div className={s.contacts}>
                        <div className={s.contactsEdit}>
                    <p className={s.contactsName}>contacts</p>
                    <button className={s.editButton} type="submit">save</button></div>
                            <div className={s.contactsItem}>
                                <img src={logoInst}></img>
                                <Field name='inst' type='text' />
                            </div>
                            <div className={s.contactsItem}>
                                <img src={logoVk}></img>
                                <Field name='vk' type='text' />
                            </div>

                        </div>
                        <div className={s.country}>
                            <p className={s.countryName}>skills</p>
                            <Field name='skills' as='textarea' />
                        </div>
                    </div>
                    
                </div>
            </Form>
        </Formik>)
}

export default ProfileInfo;