import { usersAPI } from "../api/api"
import { updateObjectInArray } from "./utils/objects-helper"

const FOLLOW = "FOLLOW"
const UNFOLLOW = "UNFOLLOW"
const SET_USERS = "SET-USERS"
const SET_CURRENT_PAGE = "SET-CURRENT-PAGE"
const TOTAL_USERS_COUNT = "TOTAL-USERS-COUNT"
const TOGGLE_IS_FETCHING = "TOGGLE-IS-FETCHING"
const TOGGLE_IS_FOLLOWING_PROGRESS = "TOGGLE-IS-FOLLOWING-PROGRESS"

let initialState = {
    users: [],
    pageSize: 5,
    totalUsersCount: 0,
    currentPage: 1,
    isFetching: true,
    followingInProgress: [],

}
let followUnfollowFlow = async(dispatch,id, apiMethod, actionCreator) => {
    dispatch(toggleFollowingProgress(true, id))
    let response = await apiMethod(id)
    if (response.data.resultCode == 0) {
        dispatch(actionCreator(id))
    }
    dispatch(toggleFollowingProgress(false, id))
}
const usersReducer = (state = initialState, action) => {
    switch (action.type) {
        case FOLLOW:
            return {
                ...state,
                users: updateObjectInArray(state.users, action.id, 'id', {followed: true })
            }

        case UNFOLLOW:
            return {
                ...state,
                users: updateObjectInArray(state.users, action.id, 'id', {followed: false })
            }

        case SET_USERS:
            return {
                ...state,
                users: [...action.users]
            }
        case SET_CURRENT_PAGE:
            return {
                ...state,
                currentPage: action.currentPage
            }
        case TOTAL_USERS_COUNT:
            return {
                ...state,
                totalUsersCount: action.totalUsersCount
            }
        case TOGGLE_IS_FETCHING:
            return {
                ...state,
                isFetching: action.isFetching
            }
        case TOGGLE_IS_FOLLOWING_PROGRESS:
            return {
                ...state,
                followingInProgress: action.isFetching
                    ? [...state.followingInProgress, action.id]
                    : [state.followingInProgress.filter(id => id != action.id)]
            }

        default:
            return state;

    }
}
export const followSuccess = (id) => ({
    type: FOLLOW,
    id: id
})
export const unfollowSuccess = (id) => ({
    type: UNFOLLOW,
    id: id
})
export const setUsers = (users) => ({
    type: SET_USERS,
    users: users
})
export const setCurrentPage = (pageNumber) => ({
    type: SET_CURRENT_PAGE,
    currentPage: pageNumber
})
export const setTotalUsersCount = (totalCount) => ({
    type: TOTAL_USERS_COUNT,
    totalUsersCount: totalCount
})
export const toggleIsFetching = (isFetching) => ({
    type: TOGGLE_IS_FETCHING,
    isFetching: isFetching
})
export const toggleFollowingProgress = (isFetching, id) => ({
    type: TOGGLE_IS_FOLLOWING_PROGRESS,
    isFetching: isFetching,
    id: id
})

export const follow = (id) => {
    return async (dispatch) => {
        followUnfollowFlow(dispatch, id,  usersAPI.follow.bind(id), followSuccess)
    }
}
export const unfollow = (id) => {
    return async (dispatch) => {
        followUnfollowFlow(dispatch, id, usersAPI.unfollow.bind(id), unfollowSuccess)
    }
}

export const getUsers = (currentPage, pageSize) => {
    return async (dispatch) => {
        dispatch(toggleIsFetching(true));
        let data = await usersAPI.getUsers(currentPage, pageSize)
        dispatch(setUsers(data.items));
        dispatch(setTotalUsersCount(data.totalCount));
        dispatch(toggleIsFetching(false));
    }
}

export default usersReducer;