import React from "react";
import { connect } from "react-redux";
import Profile from "./Profile";
import {
    Navigate,
    useLocation,
    useNavigate,
    useParams,
} from "react-router-dom";
import {getUserProfile, getStatus, updateStatus, savePhoto, saveProfile} from "../../redux/profileReducer"
import { withAuthRedirect } from "../../hoc/withAuthRedirect";
import { compose } from "redux";

class ProfileContainer extends React.Component {
    refreshProfile (){
        let userId = this.props.router.params.userId;
        if (!userId){
            userId = 23770

        }
        this.props.getUserProfile(userId)
        this.props.getStatus(userId)
   }
   
    
    componentDidMount() {
        this.refreshProfile()
    }
    componentDidUpdate(prevProps){
        if (this.props.router.params.userId != prevProps.router.params.userId)
            this.refreshProfile();
    }
    
    render() {
       
  console.log('profile='+this.props.profile)
        return (
            <Profile {...this.props} saveProfile={this.props.saveProfile} savePhoto={this.props.savePhoto} isOwner={!this.props.router.params.userId} profile={this.props.profile} status={this.props.status} updateStatus={this.props.updateStatus}/>
        )
    }
}


let mapStateToProps = (state) => ({
    profile: state.profileReducer.profile,
    status: state.profileReducer.status
})
function withRouter(Component) {
    function ComponentWithRouterProp(props) {
        let location = useLocation();
        let navigate = useNavigate();
        let params = useParams();
        return (
            <Component
                {...props}
                router={{ location, navigate, params }}
            />
        );
    }

    return ComponentWithRouterProp;
}
export default compose(
    connect(mapStateToProps, {getUserProfile, getStatus, updateStatus, savePhoto, saveProfile}),
    withAuthRedirect,
    withRouter)
    (ProfileContainer)
