import React from "react";
import { NavLink } from "react-router-dom";
import s from "./Header.module.css"
import logo from "../../assets/images/logo_react.png"

const Header = (props) => {

    return (
        <header className={s.header}>
            <div className={s.headerContent}>
                <div className={s.logo}>
                    <img src={logo}></img>
                </div>
                <div className={s.loginBlock}>
                    {props.isAuth
                        ? <div>
                            <p className={s.loginName}><div className={s.name}>{props.login}</div>
                                <button className={s.logoutButton} onClick={props.logout}>Log out</button></p>
                        </div>
                        :
                        <NavLink className={s.loginButton} to='/login'>Login</NavLink>}
                </div>
            </div>
        </header>
    )
}

export default Header;