import React from "react";
import s from "./Post.module.css"
import userPhoto from "../../../../assets/images/avatar.jpg"
import deleteIcon from "../../../../assets/images/deletePost.svg"
import likeIcon from "../../../../assets/images/likeIcon.png"


const Post = (props) => {

    return (
        <div className={s.item}>
            <div className={s.profilePost}>
            {props.profile ?  <img src={props.profile.photos.large || userPhoto}></img> :<img  src={ userPhoto}></img>}
                 
                {props.profile ? <p>{props.profile.fullName}</p> : <p>Null</p>}
                <div className={s.deletePost}>
                    <img src={deleteIcon}></img>
                </div>
            </div>
            <div className={s.postMessage}>
                <p>{props.message}</p>
                </div>
            
            <div className={s.likes}>
                <img src={likeIcon}></img>
                <div className={s.likesNum}>
                <p> {props.likes}</p>
                </div>
            </div>

        </div>

    )
}

export default Post;